.footer {
  padding: 80px 0;
  background: linear-gradient(180deg, #15007f 0%, #2600e5 100%);

  @include tablet {
      padding: 40px 0;
    }

  &__image {
    margin-bottom: 80px;

    @include tablet {
      width: 80%;
      margin-bottom: 60px;
    }

    @include mobile {
      width: 100%;
      margin-bottom: 40px;
    }
  }

  &__top {
    margin-bottom: 111px;
    display: flex;
    gap: 390px;

    &-left {
      @include small-tablet {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }

    &-right {
      display: flex;
      gap: 126px;

      @include tablet {
        gap: 60px;
      }
    }

    @include small-desktop {
      gap: 300px;
    }

    @include tablet {
    margin-bottom: 60px;
      justify-content: space-between;
      gap: 40px;
    }

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }



  &__phone-link {
    margin-bottom: 40px;
    display: block;
    font-weight: 400;
    font-size: 40px;
    line-height: 0.985;
    letter-spacing: -0.02em;
    color: var(--white);
    transition: var(--main-trs);

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: var(--outline);
      outline-color: var(--white);
      outline-offset: var(--outline-offset);
    }

    &:hover {
      color: var(--yellow);
    }

    @include small-tablet {
      margin-bottom: 20px;
      font-size: 30px;
    }
  }

  &__mail-link {
    margin-bottom: 40px;
    display: block;
    font-weight: 400;
    font-size: 24px;
    line-height: 0.985;
    letter-spacing: -0.02em;
    color: var(--white);
    transition: var(--main-trs);

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: var(--outline);
      outline-color: var(--white);
      outline-offset: var(--outline-offset);
    }

    &:hover {
      color: var(--yellow);
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__socials {
    & a:hover {
      color: var(--yellow);
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--white);
    transition: var(--main-trs);

    &:hover {
      color: var(--yellow);
    }

    &--opacity {
      opacity: 0.7;
      text-decoration: underline;
      text-decoration-skip-ink: none;
    }

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: var(--outline);
      outline-color: var(--white);
      outline-offset: var(--outline-offset);
    }
  }

  &__address {
    max-width: 313px;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--white);

    @include tablet {
      font-size: 18px;
    }
  }
}