.questions {
  padding-top: 120px;
  padding-bottom: 60px;

  @include tablet {
    padding-top: 60px;
  }

  &__title {
    margin-bottom: 60px;
  }

  &__item {
    padding: 56px 30px;
    border-top: 1px solid var(--black-20);
    width: 100%;

    &:last-of-type {
      border-bottom: 1px solid var(--black-20);
    }

    @include tablet {
      padding: 30px 55px 30px 15px;
    }

    @include mobile {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
}