.stages {
  padding-top: 30px;
  padding-bottom: 68px;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-bottom: 120px;

    @include small-tablet {
      margin-bottom: 60px;
    }
  }

  &__list {
    grid-column: 5 / -1;
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  &__item {
    opacity: 0.5;
    transition: var(--main-trs);

    &:hover {
      opacity: 1;
    }

    @include tablet {
      opacity: 1;
    }
  }

  &__decoration {
    display: flex;
    gap: 60px;
    grid-column: 4 span;

    @include tablet {
      gap: 20px;
    }
  }

  &__img {
    grid-column: 3 span;
    @include mobile {
      display: none;
    }
  }

  &__number {
    grid-column: 1 span;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    color: var(--black);

    @include mobile {
      font-size: 16px;
    }
  }

  &__text {
    grid-column: 8 span;

    @include mobile {
      grid-column: 11 span;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 600;
    font-size: 40px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--black);

    @include small-tablet {
      font-size: 30px;
      margin-bottom: 25px;
    }

    @include mobile {
      font-size: 25px;
      margin-bottom: 20px;
    }
  }

  &__descr {
    margin: 0;
    max-width: 393px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--black);

    &:not(:last-of-type) {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 15px;
        max-width: 100%;
      }
    }

    @include mobile {
      font-size: 14px;
    }
  }
}