.promotion {
  position: relative;
  padding-top: 162px;
  padding-bottom: 462px;

  @include small-desktop {
    padding-top: 100px;
    padding-bottom: 200px;
  }

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: var(--container-width);
    height: 1px;
    background-color: var(--black-20);
  }

  &__container {
    position: relative;

    @include small-tablet {
      display: flex;
      flex-direction: column;
      gap: 40px;
    }
  }

  &__title,
  &__content {
    grid-column: 6 span;

    @include small-tablet {
      grid-column: 12 span;
    }
  }

  &__title {
    height: fit-content;
  }



  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--black);

    &:not(:last-of-type) {
      margin-bottom: 38px;

      @include small-tablet {
        margin-bottom: 20px;
      }
    }

    &:last-of-type {
      margin-bottom: 63px;

      @include small-tablet {
        margin-bottom: 40px;
      }
    }

    @include small-tablet {
      max-width: 600px;
    }
  }

  &__btn {
    display: inline-block;
  }


  &__bg-1,
  &__bg-2 {
    position: absolute;
    z-index: -1;

    & img {
      @include small-desktop {
        width: 70%;
      }


    }
  }

  &__bg-1 {
    top: 54%;
    left: 3%;

    @include small-desktop {
      top: 20%;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__bg-2 {
    top: 184%;
    right: 1%;

    @include small-desktop {
      top: 109%;
      right: -18%;
    }

    @include small-tablet {
      position: static;
      z-index: 0;

    }

    & img {
      @include small-tablet {
        width: 100%;
      }
    }
  }
}