.request {
  padding: 160px 0;
  background-color: var(--dark-blue);

  @include tablet {
    padding: 60px 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 48px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    color: var(--white);

    @include tablet {
      font-size: 40px;
    }
  }
}